'use client'

import { useGetExecutor } from "@/hooks/useGetExecutor"
import { useGetPriority } from "@/hooks/useGetPriority"
import { useGetStatus } from "@/hooks/useGetStatus"
import { Task } from "@/shared/types/task.types"

interface Props {
    task: Task
}

export function TabAuthorTasksInProcess({task}: Props) {
    
    const executor = useGetExecutor(task)
    const status = useGetStatus(task)
    const priority = useGetPriority(task)
    const dateCreate = task.datecreate.slice(0, 10)
    // const dateChange = task.datechange.slice(0, 10)
    const dateEstimatedCompletion = task.dateestimatedcompletion.slice(0, 10)

    return (
        <>
            <td className="border-r-1 border-gray-300 p-2">
                {priority ? priority[0].namepriority : ''}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {status ? status[0].namestatus : ''}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.nametask}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.info}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {dateCreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.datechange ? task.datechange : dateCreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {dateEstimatedCompletion}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.document}
            </td>
            <td className="border-r-1 border-gray-300 p-2" style={{ minWidth: '150px' }}>
                {`${executor?.firstname} ${executor?.lastname}`}
            </td>
        </>
    )
}
